import { useState, useEffect, useContext, useRef, createContext, Suspense, lazy } from 'react';
import { Box, styled,Checkbox } from '@mui/material';

import { io } from 'socket.io-client';

import { getMessages, newMessages } from '../../../service/api';
import { AccountContext } from '../../../context/AccountProvider';

//components
import Message from './Message';
import Footer from './Footer';
 
import Loader from '../../loader/Loader';
import DeleteChat from './deleteChat';
import { deleteFunction } from './ChatBox';
import { CiWarning } from 'react-icons/ci';
import './css/chatbox.css'

const Wrapper = styled(Box)`
background-color: #D9AFD9;
background-image: linear-gradient(0deg, #D9AFD9 0%, #97D9E1 100%);
    background-size: 50%;
    border-radius:8px;
    width:100%
    
`;

const StyledFooter = styled(Box)`
    height: 55px;
    background: #ededed;
    // position: absolute;
    width: 100%;
    // bottom: 0
`;
    
const Component = styled(Box)`
    height: 80vh;
    overflow-y: scroll;
`;

const Container = styled(Box)`
    padding: 1px 80px;
`;

const checkbox = styled(Checkbox)`
    
`


const Messages = ({ person, conversation, display, setDisplay}) => {

    const [messages, setMessages] = useState([]);
    const [incomingMessage, setIncomingMessage] = useState(null);
    const [value, setValue] = useState();
    const [file, setFile] = useState();
    const [image, setImage] = useState();
    const [offline, setOffline] = useState(false)
    const scrollRef = useRef();
   
  

    const { account, socket, newMessageFlag, setNewMessageFlag } = useContext(AccountContext);
        
    const receiverId1 = conversation?.members?.find(member => member !== account.sub);
    const {typing,setIsTyping,setGetTyping,getTyping, deleted,setDeleted} = useContext(deleteFunction)
    useEffect(()=>{
        let typeMessage = {};
        if(typing){
            typeMessage = {
                senderId: account.sub,
                receiverId: receiverId1,
                conversationId: conversation._id,
                type: 'text',
                text:typing
            };
            
            receiverId1 && socket.current.emit("typing", typeMessage)
            setIsTyping(false)
            console.log('working')
        }
        
    },[typing])
    
    useEffect(()=>{
        socket.current.on('typer',(data)=>{
            setGetTyping(true)
            // setGetTyping(data.text)
        }
        )
        setTimeout(() => {
            setGetTyping(false)
        }, 2000);
        
    },[getTyping])
    
    useEffect(() => {
        socket.current.on('getMessage', data => {
            setIncomingMessage({
                ...data,
                createdAt:Date.now().toLocaleString()
            })
        })
    }, []);
    
    useEffect(()=>{
        let deleteMessage = {
            senderId: account.sub,
            receiverId: receiverId1,
            conversationId: conversation._id,
        };
        if(deleted){

            receiverId1 && socket.current.emit("deleted", deleteMessage)
        }
    },[deleted])

    useEffect(()=>{
        socket.current.on('deletion',(data)=>{
            setDeleted(true)
        })
    },[])

    useEffect(()=>{
        socket.current.on('offline',()=>{
            setOffline(true)
            
            setTimeout(() => {
                setOffline(false)
            }, 5000);
        })
    },[getTyping])

    useEffect(() => {
        const getMessageDetails = async () => {
            let data = await getMessages(conversation?._id);
            setMessages(data);
        }
        getMessageDetails();
        setDeleted(false)
    }, [conversation?._id, person._id, newMessageFlag]);

    useEffect(() => {
        const getMessageDetails = async () => {
            let data = await getMessages(conversation?._id);
            setMessages(data);
        }
        getMessageDetails();
        setDeleted(false)
    },[deleted]);

    useEffect(() => {
        scrollRef.current?.scrollIntoView({ transition: "smooth" })
    }, [messages]);

    useEffect(() => {
        incomingMessage && conversation?.members?.includes(incomingMessage.senderId) && 
            setMessages((prev) => [...prev, incomingMessage]);
        
    }, [incomingMessage, conversation]);
    
    const receiverId = conversation?.members?.find(member => member !== account.sub);
    
    const sendText = async (e) => {
        let code = e.keyCode || e.which;
        if(!value) return;

        if(code === 13) { 
            let message = {};
            if (!file) {
                message = {
                    senderId: account.sub,
                    receiverId: receiverId,
                    conversationId: conversation._id,
                    type: 'text',
                    text: value
                };
            } else {
                message = {
                    senderId: account.sub,
                    conversationId: conversation._id,
                    receiverId: receiverId,
                    type: 'file',
                    text: image
                };
            }

            socket.current.emit('sendMessage', message);

            await newMessages(message);

            setValue('');
            setFile();
            setImage('');
            setNewMessageFlag(prev => !prev);
        } 
    }

   
    const menu = [
             "delete",
             "add" 
            ]

    return (
        <>
        <Wrapper>
            
    <div className='offline-container' style={{display:offline?'flex':'none'}}>
        <span className='offline'>User Offline <CiWarning/></span>
    </div>
            <Component>
                {
                    messages && messages.map((message, index) => (
                        <>
                          
                        <Container ref={scrollRef}>
                            <Message key={index} message={message} display={display} setDisplay={setDisplay} />
                            
                        </Container>
                        
                        </>
                    ))
                }
            {getTyping
            &&
            <div className="chat-bubble">
            <div className="typing">
            <div className="dot"></div>
            <div className="dot"></div>
            <div className="dot"></div>
            </div>
            </div>
            }
           
            </Component>
            <DeleteChat/>
     
            <Footer 
                sendText={sendText} 
                value={value} 
                setValue={setValue} 
                setFile={setFile} 
                file={file} 
                setImage={setImage}
                setIsTyping={setIsTyping}
            />
       
        </Wrapper> 
        </>
    )
  
}

export default Messages;