import { useContext, createContext, useState } from 'react';

import { Box, styled, Typography,Checkbox } from '@mui/material';
import { GetApp as GetAppIcon } from '@mui/icons-material';
import { AccountContext } from '../../../context/AccountProvider';

import { downloadMedia, formatDate } from '../../../utils/common-utils';
import { iconPDF } from '../../../constants/data';
import DeleteChat from './deleteChat';
import ChatHeader from './ChatHeader';
import { deleteFunction } from './ChatBox';
import {AiFillExclamationCircle, AiOutlineExclamationCircle} from "react-icons/ai";


const Wrapper = styled(Box)`
background-color: white; 
color: #333333;
    padding: 9px;
    max-width: 60%;
    width: fit-content;
    display: flex;
    border-top-left-radius: 20px;
    border-top-left-radius: 0px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    word-break: break-word;
    margin-top:5px
`;
    
const Own = styled(Box)`
background-color: #2979FF; 
color: white; 
    padding: 9px;
    max-width: 60%;
    width: fit-content;
    margin-left: auto;
    display: flex;
    border-bottom-right-radius: 20px;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0px;
    border-top-left-radius: 20px;
    word-break: break-word;
    margin-top:5px
`;

const Text = styled(Typography)`
    font-size: 18px;
    padding: 0 25px 0 5px;
`;

const Time = styled(Typography)`
    font-size: 8.5px;
    color: black;
    margin-top: 6px;
    word-break: keep-all;
    margin-top: 10px;
`;

const deletedChat = styled(Box)`
    font-size:5px;
`

const secreatKey = '2fE8bK7RmW9oX1Lx3T5Y6Z4DcV7nM0P'

export const DeletaCheckedChat = createContext({event:[{target:{checked:false}}],deleteMessage:[{_id:0}]})

const Message = ({ message, display, setDisplay }) => {
   
    const { account } = useContext(AccountContext);
    const {setEvent,setDeleteMessage} = useContext(deleteFunction)
   
    function deleteAmessage(e,message){
        setEvent([e])
        setDeleteMessage([message]) 
    }
  
    return (
        <>
            <DeletaCheckedChat.Provider value={''}>
        {
            account.sub === message.senderId ? 
                <Own>
                     
                    {   message.text === secreatKey?<deletedChat style={{fontSize:'18px'}}>Message Deleted <AiFillExclamationCircle/></deletedChat>
                    
                    :
                        message.type === 'file' ? <ImageMessage message={message}   /> : <TextMessage message={message} />
                    }
                    {   message.text === secreatKey?null:<input type="checkbox" style={{display:display}} onChange={(e)=>{deleteAmessage(e,message)}}/>}
                </Own>
            : 
                <Wrapper>
                    {   message.text === secreatKey?null:<input type="checkbox" style={{display:display}} onChange={(e)=>{deleteAmessage(e,message)}}/>}
                    {
                        message.text === secreatKey?<deletedChat style={{fontSize:'18px'}} > Message Deleted <AiFillExclamationCircle/></deletedChat>
                    
                    :
                        message.type === 'file' ? <ImageMessage message={message} /> : <TextMessage message={message} />
                    }
                </Wrapper>  
        }

        
        
        </DeletaCheckedChat.Provider>
        </>
    )
}

const TextMessage = ({ message }) => {
   
    const { account } = useContext(AccountContext);
    return (
        <>
            <Text>{message.text}</Text>
           {account.sub === message.senderId ? <Time style={{color:'white'}}>{formatDate(message.createdAt)}</Time>:<Time>{formatDate(message.createdAt)}</Time>}
            
        </>
    )
}

const ImageMessage = ({ message }) => {
    const { account } = useContext(AccountContext);
    return (
        <div style={{ position: 'relative' }}>
            {
                message?.text?.includes('.pdf') ?
                    <div style={{ display: 'flex' }}>
                        <img src={iconPDF} alt="pdf-icon" style={{ width: 80 }} />
                        <Typography style={{ fontSize: 14 }} >{message.text.split("/").pop()}</Typography>
                    </div>
                : 
                    <img style={{ width: 300, height: '100%', objectFit: 'cover' }} src={message.text} alt={message.text} />
            }
              {
              account.sub === message.senderId ?
              <Time style={{ position: 'absolute', bottom: 0, right: 0 ,color:'white'}}>
                   <GetAppIcon 
                    onClick={(e) => downloadMedia(e, message.text)} 
                    fontSize='small' 
                    style={{ marginRight: 10, border: '1px solid black', borderRadius: '50%', color:'white' }} />
                {formatDate(message.createdAt)}
            </Time>
                :
                <Time style={{ position: 'absolute', bottom: 0, right: 0 ,color:'white'}}>
                <GetAppIcon 
                 onClick={(e) => downloadMedia(e, message.text)} 
                 fontSize='small' 
                 style={{ marginRight: 10, border: '1px solid grey', borderRadius: '50%', color:'black' }} />
             {formatDate(message.createdAt)}
         </Time>
        }
        </div>
    )
} 
 

export function useTasks(){
    return useContext(DeletaCheckedChat)
}
export default Message;